/* 
CONSOLA 
-------------------------  */
#consola {
  display: grid;
  grid-gap: 64px;
  width: 95%;
  word-spacing: 1px;
  margin: auto;
  border-radius: var(--consola-border-radius);
  color: var(--consola-default-text-color);
}
#consola-terminal {
  overflow: hidden;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  padding: 4px;
  font-family: var(--consola-terminal-family-font);
  font-size: 0.8em;
  border: var(--consola-border);
  -webkit-box-shadow: var(--consola-box-shadow);
          box-shadow: var(--consola-box-shadow);
  border: 3px double rgb(0, 120, 120);
  background: var(--consola-terminal-bg-color);
  max-height: calc(100vh - 250px);
}
#consola-chat {
    
  overflow: hidden;
  border: var(--consola-border);
  -webkit-box-shadow: var(--consola-box-shadow);
          box-shadow: var(--consola-box-shadow);
  background: var(--consola-chat-bg-color);
}
/****** [ 1260px ] ******/
@media screen and (min-width: 1260px) {
#consola {
    height: 100%;
    max-height: 100%;
    width: 100%;
    grid-gap: 16px;
    grid-template-columns: 1fr 1fr;
}
#consola-terminal {
    max-height: calc(100vh - 250px);
}
#consola-chat {
  
    max-height: 100%;
}
}
